}

@media (max-width: 768px) {
  .burger__btn {
    display: flex;
  }
}
.--active-burger {
  opacity: 1;
  right: 40px;
  width: 90vw;
}

.--active-btn-burger {
  transition: all 1s ease;
}

.burger__btn.--active-btn-burger .burger__line:nth-child(1) {
  transition: all 1s ease;
  transform: translateY(4px) rotate(-45deg);
}
.burger__btn.--active-btn-burger .burger__line:nth-child(2) {
  transition: all 1s ease;
  transform: translateY(-3px) rotate(45deg);
}

.menu__reboot {
  display: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 60px;
  height: 60px;
  border: 1px solid #665f55;
  border-radius: 100px;
  margin-bottom: 100px;
}

@media (max-width: 768px) {
  .menu__reboot {
    display: flex;
  }
}
.reboot--active {
  animation-name: make-circle;
  animation-duration: 3s;
}

.reboot--hide {
  opacity: 0;
}

.coffee__slider {
  width: 100%;
  display: flex;
  transition: all 1s ease;
}

@media (hover: hover) {
  .menu__drinks-item:hover .menu__drinks-img {
    transform: scale(1.1);
  }
  .left-container__img-item:hover {
    background-color: #e1d4c9;
  }
  .left-container__img-item:hover .left-container__img-svg {
    stroke: #403f3d;
  }
  .icon__item-tea:hover {
    background-color: #665f55;
  }
  .icon__item-tea:hover .container_for__icon-tea {
    background-color: #e1d4c9;
  }
  .icon__item-desert:hover {
    background-color: #665f55;
  }
  .icon__item-desert:hover .container_for__icon-dessert {
    background-color: #e1d4c9;
  }
  .arrow-left:hover {
    background: #665f55;
  }
  .arrow-left:hover .arrow-left__svg {
    stroke: #e1d4c9;
  }
  .arrow-right:hover {
    background: #665f55;
  }
  .arrow-right:hover .arrow-right__svg {
    stroke: #e1d4c9;
  }
  .enjoy__btn_meny:hover .enjoy__btn_text {
    right: 98px;
  }
  .enjoy__btn_meny:hover::after {
    transition: all 0.7s ease;
    opacity: 1;
  }
  .img-container__item:hover {
    transform: scale(1.1);
  }
  .nav__item-link:hover::before,
  .nav__item-link:hover::after,
  .footer__link:hover::after,
  .footer__link:hover::before {
    width: 50%;
  }
}
html {
  scroll-behavior: smooth;
